'use strict';

import { StringHelper } from '@ms/odsp-utilities-bundle';
import ReactDOM = require('react-dom');
import React = require('react');
import { Validate } from '@microsoft/sp-client-base';

import IClientSideWebPartStatusRenderer from '../../common/webParts/IClientSideWebPartStatusRenderer';
import PlaceholderSpinner from  '../placeHolder/PlaceholderSpinner';
import { IPlaceholderSpinnerProps } from  '../placeHolder/IPlaceholderProps';
import strings from './loc/Strings.resx';
import styles from './styles/cswp-base.module.scss';

/**
 * This class provides the default implementation for displaying loading indicator and error messages
 * for webparts. The webpart host can decide to provide custom implemenation of how webparts display
 * loading indicators and error messages.
 */
export default class ClientSideWebPartStatusRenderer implements IClientSideWebPartStatusRenderer {

  private _errorId: string = 'cswp-error';
  private _errorHtml: string;

  /**
   * Display a loading spinner.
   * @param domElement - the webpart container div.
   * @param loadingMessage - the message to be displayed when the loading spinner id displayed.
   */
  public displayLoadingIndicator(domElement: HTMLElement, loadingMessage: string): void {
    Validate.isNotNullOrUndefined(domElement, 'domElement');

    const el: React.ReactElement<{}> =
      React.createElement<IPlaceholderSpinnerProps>(
        PlaceholderSpinner,
        { label: StringHelper.format(strings.LoadingStatus, loadingMessage) });
    ReactDOM.render(el, domElement);
  }

  /**
   * Clear the loading indicator.
   * @param domElement - the webpart container div.
   */
  public clearLoadingIndicator(domElement: HTMLElement): void {
    Validate.isNotNullOrUndefined(domElement, 'domElement');

    ReactDOM.unmountComponentAtNode(domElement);
  }

  /**
   * Render the provided error message in the webpart container div.
   * @param domElement - the webpart container div.
   * @param error - the error message.
   */
  public renderError(domElement: HTMLElement, error: Error | string): void {
    Validate.isNotNullOrUndefined(domElement, 'domElement');
    Validate.isNotNullOrUndefined(error, 'error');

    let divErr: HTMLDivElement =
      domElement.querySelector(`div[data-sp-id='${this._errorId}']`) as HTMLDivElement;

    if (divErr) {
      divErr.style.display = 'block';
    } else {

      if (!this._errorHtml) {
        this._errorHtml =
          `<div class='${styles.errorBox}' role='alert' aria-live='assertive'>` +
          `<span class='${styles.errorBoxText}'></span>` +
          `</div>`;
      }

      divErr = document.createElement('div') as HTMLDivElement;
      divErr.setAttribute('data-sp-id', this._errorId);
      divErr.innerHTML = this._errorHtml;
      domElement.appendChild(divErr);
    }

    const text: HTMLSpanElement = divErr.getElementsByTagName('span')[0] as HTMLSpanElement;
    text.textContent = (DEBUG && (error as Error).stack)
      ? (error as Error).stack
      : (error as Error).message || error as string;
  }

  /**
   * Clear the webpart error message.
   * @param domElement - the webpart container div.
   */
  public clearError(domElement: HTMLElement): void {
    Validate.isNotNullOrUndefined(domElement, 'domElement');

    const divErr: HTMLDivElement =
      domElement.querySelector(`div[data-sp-id='${this._errorId}']`) as HTMLDivElement;

    if (divErr) {
      divErr.style.display = 'none';
    }
  }
}